import React, {ChangeEvent, useEffect, useState} from 'react';
import {TextField} from "@mui/material";
import {useAppDispatch} from "../../../../state/redux-store";

type StatusType = {
    status: string
    updateStatus: (status: string) => void
}

export const StatusFuncComp = ({status, updateStatus}: StatusType) => {


    useEffect(() => {
        setStatusRes(status)
    }, [status])



    const [editMode, setEditMode] = useState(false)
    const [statusRes, setStatusRes] = useState(status)


    console.log(statusRes)

    const onTextChange = (e: ChangeEvent<HTMLInputElement>) => {
        setStatusRes(e.currentTarget.value)
    }

    const onClick = () => {
        setEditMode(true)
    }

    const onBlur = () => {
        setEditMode(false)
        updateStatus(statusRes);
    }


    return (
        <>
            {!editMode ?

                <div>
                    <span onDoubleClick={onClick}>{status || 'Write your status here'}</span>
                </div>

                : <div>
                    <TextField autoFocus value={statusRes} onBlur={onBlur} size={'small'} onChange={onTextChange}
                               variant={'filled'} type="text"/>
                </div>


            }
        </>
    );
};
