import {NavLink} from "react-router-dom";
import  style from './Navbar.module.css'

import React, {useEffect, useState} from "react";
import {AllNavbarType} from "./NavbarContainer";
import {slide as Menu} from 'react-burger-menu'
import {useAppDispatch, useAppSelector} from "../../state/redux-store";
import {DialogsPropsType, getUsersThunkCreator} from "../../state/users-reducer";


function Navbar(props: AllNavbarType) {

    const [menuOpenState, setMenuOpenState] = useState(false)
    const friends=useAppSelector(state=> state.usersPage.users)

    const checkActive = (isTrue?: boolean) => (!props.isTrue ? '' : style.active)
    function first(arr:any, length:number) {
        return arr.slice(0, length);
    }


    const threeRandomFriens = first(friends,3).map((user:DialogsPropsType) => <div  key={user.id} className={style.dot}>{<NavLink to={'/profile/' + user.id}>{user.name}</NavLink>}</div>)

    const showSettings = (event: any) => {
        event.preventDefault();
    }
    const closeMenu = () => {
        setMenuOpenState(!menuOpenState)
    }

    const toggleMenu = () => {
        setMenuOpenState(!menuOpenState)
    }


    let styles = {
        bmOverlay: {
            background: 'none',

        },
        bmMenu: {
            background: 'none',
            overflow: 'revert',
        },
        bmMenuWrap: {
            position: 'absolute',

            height: '100%'
        },
        reactBurgerMenuBtn: {
            width: '50px',
            height: '50px'
        },
    }


    return (
        <>

            <div className={style.burger_menu} onClick={() => toggleMenu()}><span></span></div>
            {/*<button onClick={() => toggleMenu()}/>*/}
            <Menu styles={styles}  onStateChange={(state) => setMenuOpenState(state.isOpen)}
                  width={200}>

                <nav className={style.nav}>

                    <div className={style.item}>
                        <NavLink id="home" className={style.menu_item} onClick={() => closeMenu()}
                                 to='./profile'><span>Profile</span></NavLink>
                    </div>
                    <div className={style.item}>
                        <NavLink id="users" className={style.menu_item}
                                 onClick={() => closeMenu()} to='/users'><span>Users</span> </NavLink>
                    </div>
                    <div className={style.item}>
                        <NavLink id="dialogs" className={style.menu_item} onClick={() => closeMenu()}
                                 to='/dialogs'><span>Messages</span> </NavLink>
                    </div>

                    <div className={style.item}>
                        <NavLink id="news" className={style.menu_item} onClick={() => closeMenu()} to='/news'><span>News</span></NavLink>
                    </div>
                    <div className={style.item}>
                        <NavLink id="music" className={style.menu_item} onClick={() => closeMenu()}
                                 to='/music'><span>Music</span></NavLink>
                    </div>
                    <div className={style.item}>
                        <NavLink id="settings" className='menu_item' onClick={() => closeMenu()}
                                 to='/settings'><span>Settings</span></NavLink>
                    </div>

                    <div className={style.friends}>Friends</div>
                    <div style={{display:'flex', justifyContent:'space-around'}}>
                        {threeRandomFriens}
                    </div>

                </nav>
            </Menu>
        </>
    )
}

export default Navbar;



