import * as React from 'react';
import {useEffect, useState} from 'react';

import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {
    createTheme,
    FormLabel,
    IconButton,
    InputAdornment,
    InputLabel,
    OutlinedInput,
    ThemeProvider
} from "@mui/material";
import {Visibility, VisibilityOff} from "@mui/icons-material";

import {Controller, useForm} from "react-hook-form";
import {LoginComponentType} from "./LoginComponent";
import {useNavigate} from "react-router";

import p from './Login.module.css'

type LoginUserModelType = {
    email: string,
    password: string,
    MyCheckbox: boolean
};


export default function LoginForm({loginTC, isAuth, errorLoginServer}: LoginComponentType) {

    console.log(isAuth)

    const [show, setShow] = useState<boolean>(false)
    let navigate = useNavigate();

    const {
        register,
        reset,
        handleSubmit,
        control,
        setError,
        formState: {errors, isDirty}
    } = useForm<LoginUserModelType>();

    const onSubmit = handleSubmit(data => {

        loginTC(data.email, data.password, data.MyCheckbox)
        reset()

    });

    useEffect(() => {

        if (isAuth) {
            return navigate("/profile");
        }

    }, [isAuth]);

    const theme = createTheme();


    return (
        <ThemeProvider theme={theme}>
            <div className={p.body}>
                <div style={{display: 'flex', justifyContent: 'center'}}>
                    <FormLabel>
                        <p>To log in get registered
                            <a href={'https://social-network.samuraijs.com/'}
                               target={'_blank'}> here
                            </a>
                        </p>
                        <p>or use common test account credentials:</p>
                        <p>Email: free@samuraijs.com</p>
                        <p>Password: free</p>
                    </FormLabel>
                </div>
                <Container component="main" maxWidth="xs">
                    <CssBaseline/>
                    <Box
                        sx={{
                            marginTop: 2,
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                        }}
                    >
                        <Avatar sx={{m: 1, bgcolor: 'secondary.main'}}>
                            <LockOutlinedIcon/>
                        </Avatar>
                        <Typography component="h1" variant="h5">
                            Sign in
                        </Typography>
                        <form onSubmit={onSubmit}>

                            {/*<Box component="form" onSubmit={onSubmit} noValidate sx={{mt: 1}}>*/}

                            <TextField
                                {...register("email", {
                                    required: "Field is required", maxLength: 30
                                })}

                                error={!!errors.email}
                                type='email'
                                margin="normal"
                                fullWidth
                                id="email"
                                label="Email Address"

                                autoComplete="email"
                                autoFocus
                            />
                            <div style={{color: 'red'}}>{errors.email?.message}</div>


                            <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                            <OutlinedInput
                                {...register("password", {required: "Field is required", maxLength: 30})}
                                error={!!errors.password}

                                id="outlined-adornment-password"
                                type={show ? 'text' : 'password'}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={() => setShow(!show)}
                                            onMouseDown={() => setShow(!show)}
                                            edge="end"
                                        >
                                            {show ? <VisibilityOff/> : <Visibility/>}
                                        </IconButton>
                                    </InputAdornment>
                                }
                                label="Password"
                            />

                            <div
                                style={{color: 'red'}}> {errors.password?.message ? errors.password?.message : (!isDirty && errorLoginServer)}</div>
                            {/*<div style={{color:'red'}}>{errorLoginServer? errorLoginServer : ''}</div>*/}

                            <Controller

                                name="MyCheckbox"

                                control={control}
                                defaultValue={false}
                                // rules={{ required: true }}
                                render={({field}) => <Checkbox style={{display:'inline-block',width:'30px'}} size='small' {...field} />}

                            />

                            <span style={{paddingTop:'10px', position:'absolute'}}>Remember Me</span>

                            <Button
                                style={{backgroundColor: '#6c7272'}}
                                type="submit"
                                fullWidth
                                variant="contained"

                            >
                                Sign In
                            </Button>

                        </form>
                        {/*</Box>*/}
                    </Box>

                </Container>

            </div>
        </ThemeProvider>
    )
}












